@import '../../styles/utils';

.container {
  padding: 24px 16px;

  @include onTablet {
    padding: 24px 24px;
  }

  @include onDesktop {
    margin-inline: auto;
    padding: 24px 0 0;
    max-width: 1136px;
  }
}

.icons {
  position: relative;
  display: flex;
  gap: 8px;
  line-height: 100%;
}

.icon {
  display: block;
  height: 16px;
  width: 16px;
  background-repeat: no-repeat;

  &__text {
    position: absolute;
    left: 48px;
    top: 10%;
    font-weight: 600;
    color: $color-secondary;
    border-bottom: 1px solid $color-primary;
  }

  &--home {
    background-image: $img-home;
  }

  &--arrow {
    background-image: url(../../images/icons/Arrow.png);
    pointer-events: none;
  }
}

.article {
  &--title {
    font-family: 'Mont', sans-serif;
    margin: 24px 0 0;
    box-sizing: border-box;
    font-size: 32px;
    font-weight: 800;
    line-height: 41px;
    color: $color-primary;

    @include onTablet {
      font-size: 48px;
      margin: 40px 0 0;
    }
  }

  &--count-of-models {
    font-family: 'Mont', sans-serif;
    font-weight: 500;
    margin: 8px 0 0;
    color: $color-secondary;
  }
}

.select {
  @include pageGrid;
  margin: 32px 0 24px;

  @include onTablet {
    margin: 40px 0 24px;
  }

  &__sortByCategory {
    grid-column: 1 / 3;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__sortByNumber {
    grid-column: 3 / 5;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: 6 / 11;
    }
  }

  &__sortByCategoryText {
    grid-column: 1 / 3;
    margin-bottom: 5px;
    font-family: 'Mont', sans-serif;
    font-weight: 700;
    color: $color-secondary;

    @include onTablet {
      grid-column: 1 / 4;
    }

    @include onDesktop {
      grid-column: 1 / 6;
    }
  }

  &__sortByNumberText {
    grid-column: 3 / 5;
    margin-bottom: 5px;
    font-family: 'Mont', sans-serif;
    font-weight: 700;
    color: $color-secondary;

    @include onTablet {
      grid-column: 4 / 7;
    }

    @include onDesktop {
      grid-column: 6 / 11;
    }
  }
}
